/* style.css */

/* Reset default margin and padding */
body, html {
    margin: 0;
    padding: 0;
    height: 100%;
}

/* Ensure the body takes up the entire viewport */
body {
    overflow: hidden; /* Prevent scrolling */
    width: 100%;
    height: 100%;
}

/* Style the canvas to fit the entire viewport */
canvas {
    display: block;
    width: 100%;
    height: 100%;
}
